const OSS = require('ali-oss');
const axios = require('axios');
const {STS_API,BUCKET_REGION,BUCKET_NAME} = require('@/setting')

// config,/src/seting.js
const config = {
    api:STS_API,            //sts
    region: BUCKET_REGION,  //Bucket1yourRegionoss-cn-hangzhou
    bucket: BUCKET_NAME     //Bucketexamplebucket
}

// asyncawait
/**
 * STSOSS Client
 * @returns OSS Client
 */
async function getOssClient() {
    let  {data}  = await axios.get(config.api)
    let uploader = new OSS({
        region: config.region,
        accessKeyId: data.AccessKeyId,
        accessKeySecret: data.AccessKeySecret,
        stsToken: data.SecurityToken,
        bucket: config.bucket,
        refreshSTSToken: async () => {
            const { data } = await axios.get(config.api);
            return {
                accessKeyId: data.AccessKeyId,
                accessKeySecret: data.AccessKeySecret,
                stsToken: data.SecurityToken,
            }
        },
    })
    // console.log(uploader)   //client
    return uploader         //PromiseclientPromiseResultasyncPromise
}

module.exports = getOssClient 